.colors-gal-container {
	display: flex;
}

.colors-gal {
	width: 64px;
	height: 64px;
	margin-right: 4px;
	margin-bottom: 4px;
}

.app-menu {
	padding: 2px 17px;
	position: fixed;
	z-index: 999;
	bottom: 0;
	left: 0;
	right: 0;
	background: rgba(255, 255, 255, 0.85);
	backdrop-filter: blur(5px);
	border-top: 1px solid #ddd;
}

.app-menu-btn {
	padding: 5px;
	font-size: 0.6rem;
	color: #555;
}
.app-menu-btn i {
	margin-bottom: 2px;
	font-size: 1.5rem;
	color: #9e9e9e;
}

.app-menu-btn.active,
.app-menu-btn.active i {
	color: var(--primary);
}
